 */






.horizontal {
    grid-column: span 1;
}

.vertical {
    grid-row: span 2;
}

.big {
    grid-column: span 2;
    grid-row: span 2;
}

 
.container > div > img {
   width: 100%;
   height: 100%;
   object-fit: cover;
} 

.title{
    font-size: 1em;
    max-width:100%;
    padding: .1em 4em;
    color: black;
    font-weight: 700;
    box-shadow: 2px grey;
}
.title > h1{
    box-shadow: 2px 2px lightgrey;
}

@media (max-width: 600px){
    .title{
        font-size: 12px;
    }
    .title > h1{
        box-shadow: 2px 2px lightgrey;
    }
}

@media (max-width: 1000px){
    .title{
        font-size: 15px;
    }
    .title > h1{
        box-shadow: 2px 2px lightgrey;
    }
}






